import React, {useState, useEffect} from 'react';

import {
    Button,
    Dialog, DialogContent, DialogTitle, DialogActions
} from "@material-ui/core";

import {Form} from 'react-bootstrap';

function EditServiceDialog({active, setActive, service_id, service_name}) {
    const [service, setService] = useState("");

    function onSubmitService(){
        fetch(process.env.REACT_APP_API+'services/'+ service_id +'?format=json',{
            method: 'PUT',
            headers:{
                'Accept':'application/json',
                'Content-Type':'application/json',
                'Authorization': "Token " + localStorage.getItem('id_token')
            },
            body: JSON.stringify({
                "name": service
            }) 
        })
    };

    useEffect(() => {
        setService(service_name);
    }, [service_name])

    return (
        <>
            <Dialog open={active} onClose={setActive} maxWidth="xs" fullWidth>
                <DialogTitle>Изменить название сервисной компании</DialogTitle>
                <Form name="editservice" onSubmit={onSubmitService}>
                    <DialogContent>
                        <Form.Group>
                            <Form.Control
                            type="text"
                            id="edit_service"
                            onChange={e => setService(e.target.value)}
                            value={service}
                            />
                        </Form.Group>
                    </DialogContent>
                <DialogActions>
                    <Button type="submit" onClick={setActive} style={{outline: "none"}}>Сохранить</Button>
                </DialogActions>
                </Form>
            </Dialog>   
        </>
    )
}

export default EditServiceDialog;